import PropTypes from 'prop-types';
import { StyledLink } from "./BackLink.styled";

const BackLink = ({ to, children }) => {
    return (
        <StyledLink to={to}>
            {children}
        </StyledLink>
    );
};

BackLink.propTypes = {
    to: PropTypes.string,
    children: PropTypes.any
}

export default BackLink;